
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
components: {
Confirm
},
data(){
return {
sendMsg: '发送验证码',
errorMsg: '',
timerFinish: true,
phoneNum: '',
code: '',
loadingText: '努力加载中',
showLoading: false,
showAlert: false
}
},
methods: {
sendCode(){
if(this.timerFinish){
this.timerFinish = false;
let count = 10;
this.sendMsg = `请稍后${--count}s`;
let timer = setInterval(()=>{
count--;
this.sendMsg = `请稍后${count}s`;
if(count <= 0){
clearInterval(timer);
this.timerFinish = true;
this.sendMsg = '发送验证码';
}
},1000);

}
},
login(){
this.$router.replace('report');

},
onConfirm(){
console.log("去下载");
}
}
}



