
import XTable from 'vux/src/components/x-table/index.vue'

export default {
components: {
XTable
},
data(){
return {
showLoading: false,
loadingText: '努力加载中',
index: 0,
group: [],
lifeDays: '20',
lessonCount: '50',
minutes: '200',
lastweekLifeDays: '5',
lastweekLessonCount: '4',
lastweekMinutes: '100',
lastweekMorethan: '96%',
studyProgress: `<tr>
<td>日期</td>
<td id='lesson-name'>课程名</td>
<td>学习时长</td>
</tr>`,
showDetail: false,
studyDetail: `
<tr>
<td>日期</td>
<td>答题数</td>
<td>正确数</td>
</tr>
`
}
},
methods: {
getGroup(){
this.group.push('张三');
this.group.push('李四');
this.group.push('王五');
},
ongroupClick(index){
this.index = index;
},
onDetailClick(){
this.showDetail  = !this.showDetail;
},
onTaskClick(){
this.showLoading = true;
},
getDetail(){
let arr = [{date:'7月9日', week:'周一', name: '第一课', time:'11分钟', answerSum: '10', rightSub: '10'},{date:'7月10日', week:'周一', name: '第二课第二课第二课第二课第二课第二课第二课第二课第二课第二课第二课第二课', time:'11分钟',answerSum: '10', rightSub: '10'},{date:'7月11日', week:'周三', name: '第三课', time:'11分钟',answerSum: '10', rightSub: '10'}];
arr.forEach((obj)=>{
this.studyProgress += `
<tr>
<td><span>${obj.date}</span><br/><span>${obj.week}</span></td>
<td id='lesson-name'>${obj.name}</td>
<td>${obj.time}</td>
</tr>
`;
this.studyDetail += `
<tr>
<td><span>${obj.date}</span><br/><span>${obj.week}</span></td>
<td id='lesson-name'>${obj.answerSum}</td>
<td>${obj.rightSub}</td>
</tr>
`;
});
}
},
mounted(){
this.getGroup();
this.getDetail();
}
}



